* {
  font-family: Helvetica, Arial, sans-serif;
}

body {
  background-color: #21232a;
  color: #fff;
  padding: 30px 60px;
}

input {
  border-radius: 10px;
  color: gray;
  font-size: 1.1rem;
  padding: 8px;
  width: 100%;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p {
  padding: 10px;
  text-align: center;
}

.card img {
  max-height: 170px;
  width: 100%;
}
